<template>
	<div id="add_coupon">
		<input class="add-input" type="text" placeholder="输入优惠券卡号" ref='input' v-model="cardNum"/>
		<div class="add-btn" :class="{active:isActive}" @click="addCoupon">添加优惠券</div>
	</div>
</template>

<script>
	
export default {
	data(){
		return {
			cardNum:'',
		}
	},
  components: {
  	
  },
  computed:{
		isActive(){
			let flag = false
			if(this.cardNum.length>0){
				flag = true
			}
			return flag
		}
	},
  methods:{
		addCoupon(){
			if(this.cardNum.length>0){
				this.$Api.getCouponSubmit(this.cardNum).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.$toast('添加优惠券成功');
						this.$router.go(-1)
					}else{
						this.$toast('添加优惠券失败');
					}
				})
			}else{
				this.$toast('请输入优惠券卡号');
			}
		},
  },
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#add_coupon{
		padding: 0 0.4rem;
		.add-input{
			display: block;
			font-size: 0.4rem;
			width: 9.2rem;
			height: 0.87rem;
			// padding-bottom: 0.49rem;
			// margin-top: 0.48rem;
			border-bottom: 1px solid $border-color;
		}
		.add-btn{
			width: 9.2rem;
			height: 1.2rem;
			line-height: 1.2rem;
			text-align: center;
			border-radius: 0.6rem;
			background: #ccc;
			font-size: 0.45rem;
			color: #fff;
			margin: 0.67rem auto;
		}
		.active{
			@include bg-color;
		}
	}
</style>


