<template>
	<div id="add-coupon">
		<common-header :title="title"></common-header>
		<div class="add-wrap">
			<add-coupon></add-coupon>
		</div>
	</div>
</template>

<script>
import AddCoupon from '@/components/Coupon/AddCoupon'
import CommonHeader from '@/components/Header/CommonHeader'

export default {
	
	data(){
		return {
			title:'添加优惠券'
		}
	},
  components: {
  	AddCoupon,
  	CommonHeader
  },
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#add-coupon{
		.add-wrap{
			@include headerSpace;
		}
	}
</style>


